body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.practice-styles {
  background-color: hsla(0, 100%, 50%, 0.3);
  color: black;
  width: 300px;
  height: 350px;
  padding-top: 10px;
  position: relative;
}

.header {
  padding: 10px 0;
  border: 1px solid white;
  width: 70%;
  margin: 0 auto;
  box-shadow:5px 5px 15px hsla(0, 100%, 30%, 0.3);;
}

.reminder_set {
  padding: 10px 0;
  width: 95%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.reminder_set .reminder_button {
  width: 70%;
  padding: 10px 0;
  font-size: 14px;
  background: #EC9192;
  border: 1px solid white;
  cursor: pointer;
}
.reminder_set .reminder_form {
  text-align: left;
  width: 100%;
}
.reminder_set .reminder_save {
  margin: 0 15%;
}

.timers-container {
	background-color: white;
	padding: 15px;
  margin: 5px 5px;
  height: 60%;
  overflow-y: scroll;
}
.timer-tile-container {
	background-color: gray;
	padding: 15px;
	margin: 5px 5px;
}
